
.order_detail {
    position: relative;
    .house_img {
        position: relative;
        .house_menu {
            position: absolute;
            top: 0;
            left: 0;
            padding: 5%;
            i {
                font-size: 0.3rem;
                color: #fff;
                padding: 0.2rem;
                border-radius: 50%;
                background: rgba(0,0,0,.2);
            }
        }
    }
    .order_body {
        width:90%;
        margin: 0.2rem auto;
        .order_title {
            margin-bottom: 0.5rem;
            p {
                font-size: 0.26rem;
                color: #767676;
                line-height: 0.5rem;
            }
            h2 {
                font-size: 0.5rem;
                font-weight: bold;
                color: #484848;
            }
        }
        .house_box {
            padding: 0.4rem 0;
            border-top: 1px solid #eee;
            h3 {
                font-size: 0.32rem;
                line-height: 0.6rem;
            }
            p {
                font-size: 0.3rem;
                line-height: 0.6rem;
            }
        }
        .order_menu {
            display: flex;
            justify-content: space-between;
        }
    }
}